<template>
  <div>
    <label></label>
    <div class="xk-input">
      <input
        v-model="data.src"
        type="url"
        placeholder=""
        autofocus
      />
    </div>
    <label></label>
    <div class="xk-input">
      <input v-model="data.alt" type="text" placeholder="" />
    </div>
    <template v-if="openImgUpload">
      <div class="xk-input xk-col-12">
        <input id="img-upload" type="file" />
      </div>
      <div class="xk-input xk-col-12 img-upload-sub">
        <button class="xk-button" @click="imgUpload()"></button>
      </div>
    </template>
  </div>
</template>

<script>
import store, { mapState } from "../../store";
export default {
  name: "image-item",
  computed: {
    ...mapState({
      data: state => state.toolbarModal.data,
      openImgUpload: state => (state.setting.xkSetting.imgUpload ? true : false)
    })
  },
  methods: {
    imgUpload() {
      store.actions.imgUpload();
    }
  }
};
</script>
