import store from "../store";

export default {
  switchPreview: {
    title: "",
    operate: "switchPreview",
    icon: "eye",
    active: false,
    handler() {
      store.actions.execCommand("switchPreview");
    },
    watcher: {
      obj: "previewShow",
      handler() {
        this.active = store.state.previewShow === "hide";
      },
      deep: true
    }
  },
  fullPreview: {
    title: "",
    operate: "fullPreview",
    icon: "tv",
    active: false,
    handler() {
      store.actions.execCommand("fullPreview");
    },
    watcher: {
      obj: "previewShow",
      handler() {
        this.active = store.state.previewShow === "full";
      },
      deep: true
    }
  },
  fullScreen: {
    title: "",
    operate: "fullScreen",
    icon: "arrows-alt",
    handler() {
      store.actions.execCommand("fullScreen");
    }
  }
};
